import React, { Component } from 'react';
import $ from 'jquery';

class Reserv6 extends Component {
    render() {
        const { poster } = this.props;
        const { data } = this.props;
        const { star } = this.props;
        return(
            <section className="reserv-step reserv-step__6 clearfix">
                <div className="reserv-pay__left">
                    <h3 className="reserv-box__title">예매 공연정보</h3>
                    <ul className="reserv-info__list">
                        <li>
                            <em className="reserv-info__subject">공연명</em>
                            <p className="reserv-info__txt">{poster.pName}</p>
                        </li>
                        <li>
                            <em className="reserv-info__subject">공연장</em>
                            <p className="reserv-info__txt">{poster.pPlace}</p>
                        </li>
                        <li>
                            <em className="reserv-info__subject">관람일</em>
                            <p className="reserv-info__txt">{poster.pDate}</p>
                        </li>
                    </ul>

                    <h3 className="reserv-box__title">좌석정보</h3>
                    <ul className="reserv-info__list seat-info">
                        <li>
                            <em className="reserv-info__subject">좌석</em>
                            <p className="reserv-info__txt">
                                <ul className="info-seat__list clearfix">
                                    {data.seatList.map(seat => {
                                        return <li>{seat.seat}</li>
                                    })}
                                </ul>
                            </p>
                        </li>
                    </ul>

                    <h3 className="reserv-box__title">결제금액</h3>
                    <ul className="reserv-info__list">
                        <li>
                        <em className="reserv-info__subject">티켓 총 <strong>{data.number}매</strong></em>
                            <p className="reserv-info__txt"><strong>{data.price} STAR</strong></p>
                        </li>
                        <li>
                            <em className="reserv-info__subject">총 결제금액</em>
                            <p className="reserv-info__txt"><strong>{data.price} STAR</strong></p>
                        </li>
                    </ul>
                </div>
                <div className="reserv-pay__right clearfix">
                    <div className="reserv-pay__btns">
                        <a href="javscript:void(0)" className="k-sm__btn charge__btn">STAR 충전</a>
                        <a href="javscript:void(0)" className="k-sm__btn">STAR 확인</a>
                        <p className="reserv-pay__alert">* 1 STAR = 1 KRW</p>
                    </div>
                    <div className="reserv-star__box">
                        <ul className="my-star__wrap">
                            <li className="my-star">
                                <em>현재 보유 STAR</em>
                                <p>{star.normal + star.bonus} STAR</p>
                            </li>
                            <li>
                                <em>일반 STAR</em>
                                <p>{star.normal} STAR</p>
                            </li>
                            <li>
                                <em>보너스 STAR</em>
                                <p>{star.bonus} STAR</p>
                            </li>
                        </ul>
                        <ul className="using-star">
                            <li>
                                <em>결제 예정 STAR</em>
                                <p>{data.price} STAR</p>
                            </li>
                            <li>
                                <em>일반 STAR 사용</em>
                                <p>{data.price} STAR</p>
                            </li>
                            <li>
                                <em>보너스 STAR 사용</em>
                                <p><input type="number" id="bonusStar" name="Bonus"/>STAR</p>
                            </li>
                            <li>
                                <em>부족한 STAR</em>
                                <p>50000 STAR</p>
                            </li>
                            <li>
                                <em>결제 예정 STAR</em>
                                <p>0 STAR</p>
                            </li>
                            <li>
                                <em>결제 후 잔액</em>
                                <p>10000 STAR</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </section>
        );
    }
}

export default Reserv6;