import React, { Component } from 'react';
import $ from 'jquery';

class Popup extends Component {
    static defaultProps = {
        data : [
            {
                tInfo : [
                {
                    tNum : "dd",
                    tSeat : "dd"
                }
            ]}
        ],
    }
    componentDidMount() {
        console.log(this.props.data);
    }
    closePopup = () => {
        $(".k-popup__wrap").fadeOut(300).removeClass("active");
    }
    render() {
        const { data } = this.props;
        return (
            <section className="k-popup__wrap">
                <div className="k-dimmer"></div>
                <article className="k-popup">
                    <header className="k-popup__hd">예매 상세 내역</header>
                    <div className="k-popup__body clearfix">
                        <img src={require("../images/posters/" + data.img)} alt="" className="k-popup__img"/>
                        <ul className="popup-detail__list clearfix">
                            <li>
                                <em className="popup-detail__subject">공연명</em>
                                <p className="popup-detail__txt">{data.title}</p>
                            </li>
                            <li>
                                <em className="popup-detail__subject">관람일</em>
                                <p className="popup-detail__txt">{data.conDate}</p>
                            </li>
                            <li>
                                <em className="popup-detail__subject">공연장</em>
                                <p className="popup-detail__txt">{data.conPlace}</p>
                            </li>
                            <li>
                                <em className="popup-detail__subject">수령방법</em>
                                <p className="popup-detail__txt">{data.rMethod}</p>
                            </li>
                            <li>
                                <em className="popup-detail__subject">결제수단</em>
                                <p className="popup-detail__txt">{data.pWay}</p>
                            </li>
                            <li>
                                <em className="popup-detail__subject">결제금액</em>
                                <p className="popup-detail__txt">{data.price}</p>
                            </li>
                        </ul>
                        <p className="k-popup__txt">
                            티켓/좌석 정보
                            <small>( QR코드는 모바일 앱에서 확인 가능합니다. )</small>
                        </p>
                        <ul className="popup-seat__list clearfix">
                            {data.tInfo.map((info) => {
                                return <li>
                                    <div className="popup-seat__box clearfix">
                                        <em className="popup-seat__subject">티켓번호</em>
                                        <p className="popup-seat__txt">{info.tNum}</p>
                                    </div>
                                    <div className="popup-seat__box clearfix">
                                        <em className="popup-seat__subject">좌석</em>
                                        <p className="popup-seat__txt">{info.tSeat}</p>
                                    </div>
                                </li>;
                            })}
                        </ul>
                    </div>
                    <footer className="k-popup__ft">
                        <a href="javascript:void(0);" className="k-btn k-popup__btn" onClick={this.closePopup}>확인</a>
                    </footer>
                </article>
            </section>
        );
    }
}

export default Popup;