import React, { Component } from 'react';
import $ from 'jquery';

class Reserv1 extends Component {
    setNumber = (e) => {
        this.props.onChange(e.target.value);
    }
    render() {
        const { data } = this.props;
        return(
            <section className="reserv-step reserv-step__1">
                <div className="reserv-wrap">
                    <h2 className="reserv-step__title">관람 인원을 선택해주세요</h2>
                    <p className="reserv-step__alert">* 단체 관람은 전화로 문의주세요 (1899-3197)</p>
                    <select className="reserv-step__select" value={data.number} onChange={this.setNumber}>
                        <option value="1">1명</option>
                        <option value="2">2명</option>
                        <option value="3">3명</option>
                        <option value="4">4명</option>
                        <option value="5">5명</option>
                    </select>
                </div>
            </section>
        );
    }
}

export default Reserv1;