import React, { Component } from 'react';
import $ from 'jquery';

class Reserv2 extends Component {
    setDate = (e) => {
        this.props.onChange(e.target.value);  
    }
    render() {
        const { data } = this.props;
        return(
            <section className="reserv-step reserv-step__2">
                <div className="reserv-wrap">
                    <h2 className="reserv-step__title">관람일을 선택해주세요</h2>
                    <p className="reserv-step__alert">* 단체 관람은 전화로 문의주세요 (1899-3197)</p>
                    <select className="reserv-step__select" value={data.date} onChange={this.setDate}>
                        <option value="2019-09-29 17:00">2019-09-29</option>
                        <option value="2019-10-01 17:00">2019-10-01</option>
                        <option value="2019-10-10 17:00">2019-10-10</option>
                    </select>
                </div>
            </section>
        );
    }
}

export default Reserv2;