import React, { Component } from 'react';
import $ from 'jquery';

class InquiryList extends Component {
    static defaultProps = {
        data : []
    }
    componentDidMount() {
        $(".inquiry-list.k-list__body").click(function(){
            let isActive = $(this).hasClass("active");
            let $qna = $(this).find(".inquiry-qna");
            let qnaHeight;
            if(isActive) {
                qnaHeight = 62;
                $(this).removeClass("active");
            }
            else {
                qnaHeight = $qna.outerHeight() + 62;
                $(this).addClass("active");
            }
            $(this).css({"height":qnaHeight+"px"});
        });
    }

    changeToForm = () => {
        this.props.onCreate(true);
    }

    render() {
        const { data } = this.props;
        return(
            <section className="inquiry-list__wrap">
                <a href="javascript:void(0);" className="k-btn inquiry__btn" onClick={this.changeToForm}>문의하기</a>
                <ul className="inquiry-list k-list k-list__hd clearfix">
                    <li className="list-num">번호</li>
                    <li className="list-state">상태</li>
                    <li className="list-category">분류</li>
                    <li className="list-subject">제목</li>
                    <li className="list-date">등록일</li>
                </ul>
                {data.map(info => {
                    let qnaMark;
                    if(info.state) {
                        qnaMark = <mark class="list-state__mark active">답변완료</mark>;
                    }
                    else {
                        qnaMark = <mark class="list-state__mark">답변대기</mark>
                    }
                    return <ul tabindex="0" className="k-list inquiry-list k-list__body clearfix">
                                <li className="list-num">{info.num}</li>
                                <li className="list-state">{qnaMark}</li>
                                <li className="list-category">{info.category}</li>
                                <li className="list-subject">{info.title}</li>
                                <li className="list-date">{info.date}</li>
                                <li className="inquiry-qna">
                                    <div className="inquiry-q">{info.q}</div>
                                    <div className="inquiry-a">{info.a}</div>
                                </li>
                            </ul>
                })}
            </section>
        );
    }
}

export default InquiryList;