import React, { Component } from 'react';

class Video extends Component {
    static defaultProps = {
        data : []
    }
    render() {
        const { data } = this.props;
        const list = data.map(info => (
            <div className="swiper-slide">
                <a href={info.link} target="_blank" className="k-video__link">
                    <img src={info.thumb} alt="" className="k-video__img"/>
                    <p className="k-video__title">{info.title}</p>
                </a>
            </div>
        ));
        return(
            <section className="k-video__wrap k-section-full">
                <div className="k-section">
                    <h2 className="k-section__title">홍보영상</h2>
                    <div className="k-video swiper-container">
                    <div className="swiper-wrapper">
                        {list}
                    </div>
                    <div class="swiper-button-prev k-video__arrow"></div>
                    <div class="swiper-button-next k-video__arrow"></div>
                </div>
                </div>
            </section>
        );
    }
}

export default Video;