import React, { Component } from 'react';
import PosterInfo from './PosterInfo'

class PosterList extends Component {
    static defaultProps = {
        data: []
    }
    render() {
        const { data } = this.props;
        const list = data.slice(0).reverse().map(info => (
            <PosterInfo key={info.id} info={info}/>
        ));
        return (
            <section className="k-poster__wrap">
                <div className="swiper-container k-poster__slide k-section">
                    <div className="swiper-wrapper">{list}</div>
                    <div class="swiper-button-prev k-poster__arrow"></div>
                    <div class="swiper-button-next k-poster__arrow"></div>
                 </div>
            </section>
        );
    }
}

export default PosterList;