import React, { Component } from 'react';
import { Switch, Route, Link } from 'react-router-dom';
import $ from 'jquery';
import myData from './data/data.json';
import { createBrowserHistory } from 'history';

import Home from './Home';
import Mypage from './Mypage';
import ConDetail from './ConDetail';
import Personal from './Personal';
import ReservStep from './ReservStep';
import Guide from './Guide';

import NoticeList from './NoticeList';
import NoticeDetail from './NoticeDetail'
import UserGuide from './UserGuide';
import FAQ from './FAQ';

/* Layouts */
import Header from './layouts/Header';
import Footer from './layouts/Footer';
import SideMenu from './layouts/SideMenu';
import Loading from './layouts/Loading';

class Main extends Component {
    state = myData;

    globalFunc = () => {
        console.log("dmfdmfkldnsflkndslkfnsdlkfnlskd");
        // console.log(process.env.PUBLIC_URL);
        $(".master-filter").not(".link-filter").find(".master-filter__a").click(function(){
            let $tab = $(".master-filter__tab");
            let thisIndex = $(this).index();
            $(".master-filter__a").removeClass("active");
            $tab.removeClass("active");
            $tab.eq(thisIndex).addClass("active");
            $(this).addClass("active");
        });
        $(".child-filter__a").click(function(){
            let $tab = $(".child-filter__tab");
            let thisIndex = $(this).index();
            $(".child-filter__a").removeClass("active");
            $tab.removeClass("active");
            $tab.eq(thisIndex).addClass("active");
            $(this).addClass("active");
        });
        $(".sub-filter__a").click(function(){
            let $tab = $(".sub-filter__tab");
            let thisIndex = $(this).index();
            $(".sub-filter__a").removeClass("active");
            $tab.removeClass("active");
            $tab.eq(thisIndex).addClass("active");
            $(this).addClass("active");
        });

        /* 포스터 라벨 설정 */
        $(".k-poster__wrap").each(function() {
            let nums = $(this).attr("data-num");
            let topLabel,
                conLabel;

            switch(nums) {
                case '3' : 
                    topLabel = 'day-label';
                    conLabel = 'pre-reserv'; break;
                case '6' : 
                    topLabel = 'hot-label';
                    conLabel = 'on-reserv'; break;
                case '9' : 
                    topLabel = 'end-label';
                    conLabel = 'end-reserv'; break;
            }

            $(this).find(".k-top__label").addClass(topLabel);
            $(this).closest(".k-link__wrap").siblings(".poster-btn__wrap").find(".poster-btn.right-btn").addClass(conLabel);
        });

        const thisHash = (window.location.hash).split('/')[1];
        if( thisHash === "reservStep") {
            this.setState({
                renderLayout : false
            });
        }
        else {
            this.setState({
                renderLayout : true
            });
        }

        if(['notice','user-guide','faq'].includes(thisHash)) {
            this.setState({
                renderFilter : true
            });
        }
        else {
            this.setState({
                renderFilter : false
            });
        }
    }

    closeSideMenu = () => {
        $(".k-sidemenu__wrap").fadeOut(300);
        $(".k-sidemenu").removeClass("active");
    }

    componentDidMount() {
        this.globalFunc();
    }

    componentDidUpdate(prevProps, prevState) {
        const { current } = this.state;
        // console.log(prevState.current, this.state.current);
        if(prevState.current !== current) {
            this.globalFunc();
            this.closeSideMenu();
            $(".k-loading__wrap").show();
        }
    }

    getURL = (data) => {
        this.setState({
            current : data
        },() => {
            $(".k-loading__wrap").fadeOut(300);
        });
    }

    render() {
        const { poster } = this.state,
              { notice } = this.state,
              { star } = this.state,
              { renderLayout } = this.state,
              { renderFilter } = this.state,
              { current } = this.state;
        return (
            <div>
                <Loading />
                {renderLayout ? <Header notice={notice}/> : ""}
                {renderLayout ? <SideMenu /> : ""}
                {renderFilter ? <section className="k-guide__wrap k-section">
                <h2 className="k-main__title">이용안내</h2>
                <div className="master-filter__wrap">
                    <div className="master-filter filter-3 clearfix link-filter">
                        <Link to="/notice" className={"master-filter__a " + (current === "Notice-List" || current === "Notice-Detail" ? 'active' : "")}>공지사항</Link>
                        <Link to="/user-guide" className={"master-filter__a " + (current === "User-Guide" ? 'active' : "")}>이용안내</Link>
                        <Link to="/faq" className={"master-filter__a " + (current === "FAQ" ? 'active' : "")}>FAQ</Link>
                    </div>
                </div></section> : ""}
                <Switch>
                    <Route exact path='/' render={ props => {return <Home poster={this.state.poster} promotion={this.state.promotion} video={this.state.video} onCreate={this.getURL}/>} }></Route>

                    {/* 공연 상세페이지 */}
                    {poster.map((poster, index) => {
                        return <Route exact path={'/detail/' + poster.id} render={ props => <ConDetail onCreate={this.getURL} data={poster}/> }></Route>
                    })}

                    <Route exact path='/mypage' render={ props => { return <Mypage onCreate={this.getURL} data={this.state.reservInfo} inquiry={this.state.inquiry} personal={this.state.personal}/>} }></Route>
                    <Route exact path='/personal' render={ props => <Personal onCreate={this.getURL} personal={this.state.personal}/> }></Route>

                    {poster.map((poster,index) =>{
                        return poster.reservActive ? <Route exact path={'/reservStep/' + poster.id} render={ props => <ReservStep data={poster} star={star} onCreate={this.getURL} /> }/> : "";
                    })}

                    
                    <Route exact path='/notice' render={ props => <NoticeList onCreate={this.getURL} data={notice} page="notice"/> } />
                    <Route exact path='/user-guide' render={ props => <UserGuide page="user-guide" onCreate={this.getURL}/> } />
                    <Route exact path='/faq' render={ props => <FAQ page="faq" onCreate={this.getURL}/>}/>

                    {/* 공지사항 상세페이지 */}
                    {notice.map((data, index) => {
                        return <Route exact path={'/notice/' +  data.id} render={props => <NoticeDetail onCreate={this.getURL} allNotice={notice} data={data} page="notice-detail"/>}></Route>
                    })}
                </Switch>
                {renderLayout ? <Footer /> : ""}
            </div>
        );
    }
}

export default Main;