import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class UserGuide extends Component {
    componentDidMount() {
        this.props.onCreate("User-Guide");
    }
    render() {
        return (
            <section className="k-userGuide__wrap">
                <div className="child-filter__wrap chu">
                    <div className="child-filter">
                        <a href="javascipt:void(0)" className="child-filter__a active">회원가입</a>
                        <a href="javascipt:void(0)" className="child-filter__a">예매방법</a>
                        <a href="javascipt:void(0)" className="child-filter__a">STAR 충전방법</a>
                        <a href="javascipt:void(0)" className="child-filter__a">STAR Wallet</a>
                    </div>
                </div>
                <br/>
                <div className="child-filter__tab active">
                    <img src={require('./images/guide-signup.png')} alt=""/>
                </div>
                <div className="child-filter__tab">
                    <img src={require('./images/guide-reserv.png')} alt=""/>
                </div>
                <div className="child-filter__tab">
                    <div className="sub-filter__wrap clearfix">
                        <div className="sub-filter">
                            <a href="javascript:void(0);" className="sub-filter__a active">Gift Card</a>
                            <a href="javascript:void(0);" className="sub-filter__a">KST</a>
                            <a href="javascript:void(0);" className="sub-filter__a">가상계좌</a>
                        </div>
                        <div className="sub-filter__tab active">
                            <img src={require('./images/guide-giftcard.png')} alt=""/>
                        </div>
                        <div className="sub-filter__tab">
                            <img src={require('./images/guide-kst.png')} alt=""/>
                        </div>
                        <div className="sub-filter__tab">
                            <img src={require('./images/guide-v.png')} alt=""/>
                        </div>
                    </div>
                </div>
                <div className="child-filter__tab active">
                    <img src={require('./images/guide-wallet.png')} alt=""/>
                </div>
            </section>
        );
    }
}

export default UserGuide;