import React, { Component } from 'react';
import $ from 'jquery';
import GuideSignUp from './layouts/GuideSignUp';
import GuideCharge from './layouts/GuideCharge';
import GuideWallet from './layouts/GuideWallet';

import SeatGrade from './dataSet/SeatGrade';

class ConDetail extends Component {
    static defaultProps = {
        data : []
    }
    
    openReservStep = () => {
        window.open("/ReactApp_K/#/reservStep/" + this.props.data.id ,"_blank","width=1200,height=800,toolbar=no,resizable=no");
    }

    componentDidMount() {
        this.props.onCreate("detail");
        $("html,body").scrollTop(0);
    }

    componentDidUpdate() {
        $(".master-filter__a, .master-filter__tab").removeClass("active");
        $(".master-filter__a").eq(0).addClass("active");
        $(".master-filter__tab").eq(0).addClass("active");
    }

    render() {
        const { data } = this.props;
        const detailPrice = data.detailPrice;
        return (
            <section className="k-condetail__wrap k-section-full">
                <div className="k-condetail__top clearfix">
                    <div className="k-section">
                        <div className="k-poster__wrap" data-num={data.pNum}>
                            <mark className="k-top__label">{data.pTopLabel}</mark>
                            <img className="k-poster__img" src={require('./images/posters/' + data.pImg)} alt=""/>
                        </div>
                        <div className="poster-txt__wrap">
                            <div claaName="poster-label__wrap">
                                {data.pLabel.map(function(name,index){
                                    return <mark className="poster-label">{name.title}</mark>;
                                })}
                            </div>
                            <h3 className="poster-title">{data.pName}</h3>
                            <p className="poster-date">{data.pDate}</p>
                            <p className="poster-place">{data.pPlace}
                                <a href="javascript:void(0)" class="con-map__btn">지도에서 보기</a>
                            </p>
                            <a href="javascript:void(0)" class={"k-reserv__btn k-btn " + (data.reservActive ? "" : "disabled")} onClick={(data.reservActive ? this.openReservStep : "")}>예매하기</a>
                        </div>
                    </div>
                </div>
                <div className="k-condetail__bottom k-section">
                    <div className="master-filter__wrap">
                        <div className="master-filter master-filter-4 clearfix">
                            <a href="javascript:void(0)" className="master-filter__a active">상세정보</a>
                            <a href="javascript:void(0)" className="master-filter__a">회원가입</a>
                            <a href="javascript:void(0)" className="master-filter__a">STAR충전방법</a>
                            <a href="javascript:void(0)" className="master-filter__a">STARWALLET</a>
                        </div>
                    </div>
                    <div className="master-filter__tab con-info__tab active">
                        <div className="con-info__box">
                            <h3 className="con-info__subject">관람 시간</h3>
                            <p className="con-info__txt">{data.detailDate}</p>
                        </div>
                        <div className="con-info__box">
                            <h3 className="con-info__subject">가격(STAR)</h3>
                            <ul className="con-seat__list">
                                {detailPrice.map(function(name,index){
                                    return <li className="con-seat__li">
                                    {name.seatName}
                                    <SeatGrade seatGrade={name.seatGrade}/>
                                </li>;
                                })}
                            </ul>
                        </div>
                        <div className="con-info__box">
                            <h3 className="con-info__subject">상품정보제공 공시</h3>
                            <p className="con-info__txt">{data.prDetail.txt}</p>
                            <img src={require('./images/poster-detail/' + data.prDetail.detailImg)} className="con-info__img" alt=""/>
                        </div>
                    </div>
                    <GuideSignUp />
                    <GuideCharge />
                    <GuideWallet />
                </div>
            </section>
        );
    }
}

export default ConDetail;