import React, { Component } from 'react';
import $ from 'jquery';

class Reserv5 extends Component {
    state = {
        type : "현장수령",
        state : false,
        rName1 : "",
        rContact1 : ""
    }
    handleChange = (e) => {
        [e.target.name] = e.target.value;
        if($("#rName").val() === '' || $("#rContact").val() === '') {
            this.setState({
                state : false
            });
            this.props.onChange(false,this.state.type);
        }
        else {
            this.setState({
                state : true
            });
            this.props.onChange(true,this.state.type);
        }
    }
    reserv5Type = (type) => {
        this.setState({type : type});
        this.props.onChange(this.state.state,type);
    }

    render() {
        const { type } = this.state;
        return(
            <section className="reserv-step reserv-step__5">
                <div className="master-filter__wrap">
                    <div className="master-filter filter-2 clearfix">
                        <a href="javascript:void(0);" className="master-filter__a active" onClick={() => {this.reserv5Type('현장수령')}}>현장수령</a>
                        <a href="javascript:void(0);" className="master-filter__a" onClick={() => {this.reserv5Type('페이스 티켓')}}>페이스 티켓</a>
                    </div>
                </div>
                <div className="reserv-receive__wrap r-field active">
                    <form className="k-form clearfix">
                            <h3 className="reserv-receive__title">예매자 정보 입력</h3>
                            <div className="form-input__box">
                                <input id="rName" type="text" name="rName1" className="k-form__input" value={this.state.rName} onChange={this.handleChange} />
                                <label for="rName" className="k-form__label">이름</label>
                            </div>
                            <div className="form-input__box">
                                <input id="rContact" type="phone" name="rContact1" className="k-form__input" value={this.state.rContact} onChange={this.handleChange}/>
                                <label for="rContact" className="k-form__label">연락처</label>
                            </div>
                    </form>
                    
                        {type === '페이스 티켓' ? <div className="faceticek-info">
                        <div className="what-faceticket">
                            <h4>페이스 티켓이란?</h4>
                            <p>페이스 티켓이란 현장에서 번거로운 지류티켓 확인 절차 없이<br/> 인식만으로 본임임을 인증하여 입장할 수 있는 서비스입니다.</p>
                        </div> 
                        <ul className="step-faceticket clearfix">
                            <li>KSTAR 티켓몰에서 <br/>콘서트 티켓 예매 후</li>
                            <li>본인 사진을 등록</li>
                            <li>티켓 챙길 필요 NO!<br/>얼굴 인증으로 입장 OK!</li>
                        </ul></div> : ""}
                </div>
            </section>
        );
    }
}

export default Reserv5;