import React, { Component } from 'react';

class ReservInfo extends Component {
    static defaultProps = {
        data : []
    }
    passData = (index) => {
        this.props.onCreate(this.props.data[index]);
    }
    render() {
        const { data } = this.props;
        return(
            <ul className="mypage-ticket__list">
                {data.map((info,index) => {
                    let cancelBtn;
                    if(info.pState === '결제완료') {
                        cancelBtn = <a href="javascript:void(0)" class="ticket-sm__btn">취소</a>;
                    }
                    return <li className="clearfix">
                            <img src={require("../images/posters/" + info.img)} className="mypage-ticket__img" alt=""/>
                            <div className="ticket-info__wrap">
                                <h4 className="ticket-info__title">{info.title}</h4>
                                <ul className="ticket-info__list clearfix">
                                    <li>
                                        <em className="ticket-info__subject">예매번호</em>
                                        <p className="ticket-info__txt">{info.rNum}</p>
                                    </li>
                                    <li>
                                        <em className="ticket-info__subject">결제일</em>
                                        <p className="ticket-info__txt">{info.pDate}</p>
                                    </li>
                                    <li>
                                        <em className="ticket-info__subject">예매일</em>
                                        <p className="ticket-info__txt">{info.rDate}</p>
                                    </li>
                                    <li>
                                        <em className="ticket-info__subject">결제수단</em>
                                        <p className="ticket-info__txt">{info.pWay}</p>
                                    </li>
                                    <li>
                                        <em className="ticket-info__subject">회차</em>
                                        <p className="ticket-info__txt">{info.sr}</p>
                                    </li>
                                    <li>
                                        <em className="ticket-info__subject">결제상태</em>
                                        <p className={"ticket-info__txt " + (info.pState == '결제완료' ? 'txt-purple' : 'txt-red')}>{info.pState} {cancelBtn}</p>
                                    </li>
                                    <li>
                                        <em className="ticket-info__subject">결제금액</em>
                                        <p className="ticket-info__txt">{info.price}</p>
                                    </li>
                                    <li>
                                        <em className="ticket-info__subject">티켓종류</em>
                                        <p className="ticket-info__txt">{info.tkind}</p>
                                    </li>
                                    <li>
                                        <em className="ticket-info__subject">매수</em>
                                        <p className="ticket-info__txt">{info.amount}</p>
                                    </li>
                                    <li>
                                        <em className="ticket-info__subject">현금영수증</em>
                                        <p className="ticket-info__txt">
                                            <a href="javascript:void(0)" class="ticket-sm__btn">승인번호 확인</a>
                                        </p>
                                    </li>
                                </ul>
                                <a href="javascript:void(0);" className="ticket-detail__btn" onClick={() => this.passData(index)}>티켓보기</a>
                            </div>
                        </li>;
                })}
            </ul>
        );
    }
}

export default ReservInfo;