import React, { Component } from 'react';

class SeatGrade extends Component {
    static defaultProps = {
        seatGrade : []
    }
    
    render() {
        const { seatGrade } = this.props;
        return(
            <ul className="seat-grade__list">
                {seatGrade.map(function(name,index){
                    const style = {
                        "backgroundColor" : name.color
                    }
                    return <li class="seat-grade__li">
                        <i class="seat-grade__color" style={style}></i> 
                        <span class="seat-grade__name">{name.name}</span> 
                        <em class="seat-grade__price">{name.price} STAR</em>
                    </li>;
                })}
            </ul>
        );
    }
}

export default SeatGrade;