import React, { Component } from 'react';
import $ from "jquery";
import { Link } from 'react-router-dom';

class Header extends Component {

    state = {
        noticeIndex : 1
    }

    activeSideMenu = () => {
        $(".k-sidemenu__wrap").fadeIn(300);
        $(".k-sidemenu").addClass("active");
    }

    componentDidMount() {
        let $notice = $(".k-notice__thumb .k-notice__list > li");
        let noticeRollingInterval = setInterval(this.noticeRolling, 3500);
        $notice.on({
            "mouseenter" : () => {
                clearInterval(noticeRollingInterval);
            },
            "mouseleave" : () => {
                noticeRollingInterval = setInterval(this.noticeRolling, 3500);
            },
            "focus" : () => {
                clearInterval(noticeRollingInterval);
                $notice.removeClass("notice-active notice-prev");
            },
            "blur" : () => {
                noticeRollingInterval = setInterval(this.noticeRolling, 3500);
            }
        });
    }

    noticeRolling = () => {
        let $notice = $(".k-notice__thumb .k-notice__list > li");
        let { noticeIndex } = this.state;
        let noticeLen = $notice.length - 1;

        if(noticeLen === 1) {
            return false;
        }
        else {
            $notice.removeClass("notice-active notice-prev");
            $notice.eq(noticeIndex).addClass("notice-active");
            $notice.eq(noticeIndex - 1).addClass("notice-prev");

            if(noticeLen === 2) {
                setTimeout(function(){
                    $notice.eq(noticeIndex - 1).removeClass("notice-prev");
                    this.setState({noticeIndex : noticeIndex + 1});
                    if(noticeIndex >= noticeLen) {
                        this.setState({noticeIndex : 0});
                    }
                },300);
            }
            else {
                this.setState({noticeIndex : noticeIndex + 1});
                if(noticeIndex >= noticeLen) {
                    this.setState({noticeIndex : 0});
                }
            }
        }
    }

    render() {
        const { notice } = this.props;
        return (
            <div>
                <header className="k-header">
                    <div className="k-header__banner">
                        <img src={require('../images/top-banner.jpg')} alt="4차 산업혁명으로 엔터테인먼트 사업을 선도하는 블록체인 결제 서비스!"/>
                    </div>
                    <nav className="k-navbar">
                        <div className="k-section">
                            <ul className="k-site__nav">
                                <li><a href="javascript:void(0)">STAR PAY</a></li>
                                <li><a href="javascript:void(0)">STAR KST</a></li>
                            </ul>
                            <select className="k-lang__select">
                                <option value="KOR">KOR</option>
                                <option value="ENG">ENG</option>
                                <option value="CHI">CHI</option>
                            </select>
                            <ul className="k-main__nav">
                                <li><Link to="/notice">이용안내</Link></li>
                                <li><a href="javascript:void(0)">고객센터</a></li>
                                <li><Link to="/mypage">마이페이지</Link></li>
                                <li><a href="javascript:void(0);" onClick={this.activeSideMenu}>로그인</a></li>
                            </ul>
                        </div>
                    </nav>
                    <div className="k-section-full k-search__wrap">
                        <section className="k-search__box k-section">
                                <h1 className="k-logo">
                                    <a href="/ReactApp_K/#">
                                        <img src={require('../images/logo.png')} alt="KSTAR LOGO"/>
                                    </a>
                                </h1>
                                <form className="k-search__form">
                                    <input type="text" className="k-search__input" placeholder="공연 제목을 검색해 주세요."/>
                                    <a href="javascript:void(0)" class="k-search__btn txt-hidden">검색버튼</a>
                                    <article className="k-notice__thumb">
                                        <i className="k-notice__i">Notice</i>
                                        <ul className="k-notice__list">
                                            {notice.slice(0,5).reverse().map((data,index) => {
                                                return <li className={index===0 ? "notice-active" : ""}><Link to={"/notice/" + data.id} className="k-truncate">{data.title}</Link></li>;
                                            })}
                                        </ul>
                                    </article>
                                </form>
                                <a href="http://starpay.tv" className="k-search__banner">
                                    <img src={require('../images/header-banner.jpg')} alt="STAR 충전은 여기서!"/>
                                </a>
                        </section>
                    </div>
                </header>
            </div>
        );
    }
}

export default Header;