import React, { Component } from 'react';
import Swiper from 'swiper';
import $ from 'jquery';
import { Link } from 'react-router-dom';

//import Poster from './dataSet/Poster';
import PosterList from './dataSet/PosterList';
import Promotion from './dataSet/Promotion';
import Video from './dataSet/Video';

class Home extends Component {

    componentDidMount() {
        this.props.onCreate("home");
        // 공연 슬라이드
        setTimeout(function(){
            const posterSlide = new Swiper('.k-poster__slide', {
                slidesPerView: 'auto',
                centeredSlides: true,
                spaceBetween: 20,
                loop: true,
                loopAdditionalSlides: 10,
                grabCursor: true,
                navigation: {
                    nextEl: '.k-poster__arrow.swiper-button-next',
                    prevEl: '.k-poster__arrow.swiper-button-prev',
                },
            });
        },100);
        
        // 홍보영상 슬라이드
        const videoSlide = new Swiper('.k-video', {
            slidesPerView: '5',
            spaceBetween: 12,
            grabCursor: true,
            navigation: {
                nextEl: '.k-video__arrow.swiper-button-next',
                prevEl: '.k-video__arrow.swiper-button-prev',
            },
        });

    }

    /*handleCreate = (data) => {
        let self = this;
        const { poster } = this.state;
        this.setState({poster : poster.concat({id:this.id++,...data})});

        setTimeout(function(){
            console.log(self.state.poster);
        },300);
    }*/

    render() {
        return (
            <div>
                {/* Poster Slide */}
                <PosterList data={this.props.poster}/>
                
                {/* Promotion */}
                <Promotion data={this.props.promotion} />

                {/* Ad */}
                <section className="k-ad__wrap k-section-full">
                    <Link to="/detail/10" className="k-ad k-section">
                        <img src={require('./images/img-banner.jpg')} alt=""/>
                    </Link>
                </section>

                {/* Video */}
                <Video data={this.props.video} />
            </div>
        );
    }
}

export default Home;