import React, { Component } from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';


class NoticeDetail extends Component {
    static defaultProps = {
        data : []
    }
    

    componentDidMount() {
        $("html,body").scrollTop(0);
        this.props.onCreate("Notice-Detail");
    }
    componentDidUpdate(prevProps) {
        $("html,body").scrollTop(0);
    }

    render() {
        const { data } = this.props;
        const { allNotice } = this.props;
        return (
            <section className="k-notice__detail k-section">
                    <ul className="k-list notice-list k-list__body clearfix">
                        <li className="list-num">{data.id}</li>
                        <li className="list-category">{data.category}</li>
                        <li className="list-subject">{data.title}</li>
                        <li className="list-date">{data.date}</li>
                    </ul>
                    <div className="notice-detail__content">
                        {data.content}
                    </div>
                    <div className="notice-detail__ft">
                        {allNotice.map(all => {
                            return data.id - 1 === all.id ?
                                <Link to={"/notice/" + all.id} className="k-list__link">
                                    <ul className="k-list clearfix notice-link__list">
                                        <li className="list-num list-prev">이전글</li>
                                        <li className="list-subject">{all.title}</li>
                                        <li className="list-date">{all.date}</li>
                                    </ul>
                                </Link> : "";
                        })}
                        {allNotice.map(all => {
                            return data.id + 1 === all.id ?
                                <Link to={"/notice/" + all.id} className="k-list__link">
                                    <ul className="k-list clearfix notice-link__list">
                                        <li className="list-num list-next">다음글</li>
                                        <li className="list-subject">{all.title}</li>
                                        <li className="list-date">{all.date}</li>
                                    </ul>
                                </Link> : "";
                        })}
                    </div>
                    <Link to="/notice" className="k-btn cancel-btn">목록</Link>
            </section>
        );
    }
}

export default NoticeDetail;