import React, { Component } from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';

import NoticeList from './NoticeList';
import NoticeDetail from './NoticeDetail';
import UserGuide from './UserGuide';
import FAQ from './FAQ';

class Guide extends Component {
    componentDidMount() {
        this.props.onCreate("guide");
    }
    render() {
        const { page } = this.props;
        const { notice } = this.props;
        const { allNotice } = this.props;
        return (
            <section className="k-guide__wrap k-section">
                <h2 className="k-main__title">이용안내</h2>
                <div className="master-filter__wrap">
                    <div className="master-filter filter-3 clearfix link-filter">
                        <Link to="/notice" className={"master-filter__a " + (page === 'notice' || page === 'notice-detail' ? "active" : "")}>공지사항</Link>
                        <Link to="/user-guide" className={"master-filter__a " + (page === 'user-guide' ? "active" : "")}>이용안내</Link>
                        <Link to="/faq" className={"master-filter__a " + (page === 'faq' ? "active" : "")}>FAQ</Link>
                    </div>
                </div>
                {page === 'notice' ? <NoticeList data={notice}/> : "" }
                {page === 'notice-detail' ? <NoticeDetail data={notice} allNotice={allNotice}/> : ""}
                {page === 'user-guide' ? <UserGuide /> : ""}
                {page === 'faq' ? <FAQ /> : ""}
            </section>
        );
    }
}

export default Guide;