import React, { Component } from 'react';
import $ from 'jquery';

import Reserv1 from './reservStep/Reserv1';
import Reserv2 from './reservStep/Reserv2';
import Reserv3 from './reservStep/Reserv3';
import Reserv4 from './reservStep/Reserv4';
import Reserv5 from './reservStep/Reserv5';
import Reserv6 from './reservStep/Reserv6';
import Reserv7 from './reservStep/Reserv7';

class ReservStep extends Component {
    state = {
        step : 1,
        number : "1",
        date : "2019-09-29 17:00",
        cSeat : true,
        receiptType : "",
        total : 0,
        price : 0,
        step3 : false,
        step4 : false,
        step5 : false,
        seatList : [],
        select : 0,
    }
    componentDidMount() {
        this.props.onCreate("reservStep");
        $('html,body').addClass('x-auto');
        console.log(this.props.data);
    }

    nextStep = () => {
        if(this.state.step < 7) {
            this.setState({
                step : this.state.step + 1
            });
        }
    }
    prevStep = () => {
        if(this.state.step > 1) {
            this.setState({
                step : this.state.step - 1
            });
        }
    }
    
    reservClose = () => {
        window.close();
    }
    openMypage = () => {
        window.opener.document.location.href = '/ReactApp_K/#/mypage';
        window.close();
    }

    componentDidUpdate(prevProps,prevState) {
        console.log(this.state.seatList);
        const $stepNum = $(".reserv-step__num > li");
        const $stepArrow = $(".reserv-step__hd .step-arrow ");
        const numWidth = $stepNum.eq(0).outerWidth();
        console.log(numWidth);
        const { step } = this.state;
        if(step !== prevState.step) {
            $stepNum.removeClass("active");
            $stepNum.eq(step - 1).addClass("active");
            $stepNum.removeClass("done");
            for(var i=step-1;i>0;i--) {
                $stepNum.eq(i - 1).addClass("done");
            }

            $stepArrow.css("left",(((step*numWidth)-(numWidth/2))-7.5) + "px");
        }

        if(step === 3) {
            {this.state.seatList.map(seat => {
                return $(".seat > li").eq(seat.id - 1).addClass("active");
            })} 
        }

        /* Step4 : 예매동의 초기화 */
        if(this.state.step4) {
            if(step !== 4) {
                this.setState({
                    step4 : false
                });
            }
        }


        if(step === 5) {
            const $input = $(".k-form__input");
            const $label = ".k-form__label";
            $input.focus(function() {
                $(this).siblings($label).addClass('active'); 
            });
            $input.blur(function() {
                let thisVal = $(this).val();
                if(!thisVal) {
                    $(this).siblings($label).removeClass('active');
                }
            });

            $(".master-filter").not(".link-filter").find(".master-filter__a").click(function(){
                let $tab = $(".master-filter__tab");
                let thisIndex = $(this).index();
                $(".master-filter__a").removeClass("active");
                $tab.removeClass("active");
                $tab.eq(thisIndex).addClass("active");
                $(this).addClass("active");
            });
        }
    }

    getNumber = (number) => {
        this.setState({
            number : number
        });
    }
    getDate = (date) => {
        this.setState({
            date : date
        });
    }
    getcSeat = (seatList,select,isActive,price) => {
        this.setState({
            seatList : seatList,
            select : select,
            step3 : isActive,
            price : price
        });
    }

    getStep4 = (step4) => {
        this.setState({
            step4 : step4
        });
    }

    getStep5 = (step5,type) => {
        this.setState({
            step5 : step5,
            receiptType : type
        });
    }

    render() {
        const { step } = this.state;
        const { data } = this.props;
        const { star } = this.props;
        return(
            <section className="reserv-step__wrap">
                <header className="reserv-step__hd">
                    <i className="step-arrow"></i>
                    <ul className="reserv-step__num clearfix">
                        <li className="active">인원선택</li>
                        <li>관람일 선택</li>
                        <li>좌석/가격 선택</li>
                        <li>예매동의</li>
                        <li>수령방법 선택</li>
                        <li>결제하기</li>
                        <li>예매 완료</li>
                    </ul>
                </header>
                <div className="reserv-step__body">
                    {(() => {
                        switch(step) {
                            case 1 : return <Reserv1 data={this.state} onChange={this.getNumber}/>;break;
                            case 2 : return <Reserv2 data={this.state} onChange={this.getDate}/>;break;
                            case 3 : return <Reserv3 data={this.state} poster={data} onChange={this.getcSeat}/>;break;
                            case 4 : return <Reserv4 data={this.state} onChange={this.getStep4}/>;break;
                            case 5 : return <Reserv5 data={this.state} onChange={this.getStep5}/>;break;
                            case 6 : return <Reserv6 data={this.state} poster={data} star={star}/>;break;
                            case 7 : return <Reserv7 data={this.state}/>;break;
                        }
                    })()}
                </div>
                <footer className="reserv-step__ft">
                    <div className="k-btns">
                        {step !== 1 && step !== 7 ? <a href="javascript:void(0)" className="k-btn cancel-btn" onClick={this.prevStep}>이전</a> : ""}
                        {[1,2,6].includes(step) ? <a href="javascript:void(0)" className="k-btn" onClick={this.nextStep}>다음</a> : "" }
                        {[3].includes(step) ? <a href="javascript:void(0)" className={"k-btn " + (!this.state.step3 ? "disabled" : "")} onClick={this.state.step3 ? this.nextStep : ""}>다음</a> : ""}
                        {[4].includes(step) ? <a href="javascript:void(0)" className={"k-btn " + (!this.state.step4 ? "disabled" : "")} onClick={this.state.step4 ? this.nextStep : ""}>다음</a> : ""}
                        {[5].includes(step) ? <a href="javascript:void(0)" className={"k-btn " + (!this.state.step5 ? "disabled" : "")} onClick={this.state.step5 ? this.nextStep : ""}>다음</a> : ""}
                        {step === 7 ? <a href="javascript:void(0)" className="k-btn cancel-btn" onClick={this.reservClose}>닫기</a> : "" }
                        {step === 7 ? <a href="javascript:void(0)" className="k-btn" onClick={this.openMypage}>마이페이지</a> : "" }
                    </div>
                </footer>
            </section>
        );
    }
}

export default ReservStep;