import React, { Component } from 'react';

class GuideCharge extends Component {
    render() {
        return (
            <div className="master-filter__tab">
                <div className="child-filter__wrap chu">
                    <div className="child-filter">
                        <a href="javascipt:void(0)" className="child-filter__a active">Gift Card</a>
                        <a href="javascipt:void(0)" className="child-filter__a">KST</a>
                        <a href="javascipt:void(0)" className="child-filter__a">가상계좌</a>
                    </div>
                </div>
                <br/>
                <div className="child-filter__tab active">
                    <img src={require('../images/guide-giftcard.png')} alt=""/>
                </div>
                <div className="child-filter__tab">
                    <img src={require('../images/guide-kst.png')} alt=""/>
                </div>
                <div className="child-filter__tab">
                    <img src={require('../images/guide-v.png')} alt=""/>
                </div>
                
            </div>
        )
    }
}

export default GuideCharge;