import React, { Component } from 'react';

class Promotion extends Component {
    static defaultProps = {
        data: []
    }
    render() {
        const { data } = this.props;
        const list = data.map(info => (
                <li>
                    <mark className="k-promotion__mark">{info.mark}</mark>
                    <h3 className="k-promotion__title">{info.title}</h3>
                    <img src={require('../images/' + info.img)} alt="" className="k-promotion__img"/>
                    <p className="k-promotion__txt">{info.txt}</p>
                    <a href={process.env.PUBLIC_URL + '/' + info.link} className="k-promotion__link">자세히 보기</a>
                </li>
        ));
        return (
            <section className="k-promotion__wrap k-section-full">
                <div className="k-section k-promotion">
                    <h2 className="k-section__title">상품별 프로모션</h2>
                    <ul className="k-promotion__list">
                        {list}
                    </ul>
                </div>
            </section>
        );
    }
}

export default Promotion;