import React, { Component } from 'react';

class MyInfo extends Component {
    render() {
        const { personal } = this.props;
        return (
            <div className="k-myinfo__wrap k-section-full clearfix">
                    <div className="k-myinfo wallet-info">
                        <a href="javascript:void(0)" className="k-myinfo__face">
                            <i className="face-add__icon"></i>
                        </a>
                        <div className="k-myinfo__txts">
                            <h3 className="k-myinfo__title">지갑주소</h3>
                            <p className="k-myinfo__addr">
                                <b>간편주소</b>{personal.cAddr}
                            </p>
                        </div>
                    </div>
                    <div className="k-myinfo star-info">
                        <h3 className="k-myinfo__title">보유 중인 STAR
                            <small>* 1 STAR = 1 KRW</small>
                        </h3>
                        <p className="current-star">
                            {personal.star}<small>STAR</small>
                        </p>
                        <a href="javascript:void(0)" class="refresh__btn k-sm__btn">새로고침</a>
                        <a href="javascript:void(0)" class="charge__btn k-sm__btn">STAR 충전</a>
                    </div>
            </div>
        );
    }
}

export default MyInfo;