import React, { Component } from 'react';
import $ from 'jquery';
import { isNull } from 'util';

class Reserv3 extends Component {
    static defaultProps = {
        row : 15,
        col : 15
    }

    state = {
        select : 0,
        rowNum : 0,
        colNum : 0,
        seatList : [],
        price : 0
    }

    setcSeat = (isActive) => {
        this.props.onChange(this.state.seatList,this.state.select,isActive,this.state.price);
    }

    handleSeat = (i,price,e) => {
            const $this = e.target;
            let rowNums = i % this.props.row === 0 ? Math.ceil((i+1) / this.props.col) - 2 : Math.ceil((i+1) / this.props.col) - 1;
            let colNums = i % this.props.col === 0 ? this.props.col : i % this.props.col;
            //console.log("row : " + Math.ceil((i+1) / this.props.col) + ", col : " + this.props.col);
            if(!$this.classList.contains("active")) {
                if(!(this.props.data.select >= parseInt(this.props.data.number))) {
                    $this.classList.add("active");
                    this.setState({
                        select : this.props.data.select + 1,
                        price : this.props.data.price + price
                    });

                    this.setState({
                        rowNum : String.fromCharCode((65 + rowNums)),
                        colNum : colNums
                    },() => {
                        this.setState({
                            seatList : this.state.seatList.concat({id : i, seat : this.state.rowNum + "열" + this.state.colNum + "번",price : price})
                        },() => {
                            if(this.state.select === parseInt(this.props.data.number)) this.setcSeat(true);
                            else this.setcSeat(false);
                        })
                    });
                }
                else {
                    return false;
                }
            }
            else {
                $this.classList.remove("active");
                this.setState({
                    select : this.props.data.select - 1,
                    price : this.props.data.price - price
                });
                const result = this.props.data.seatList.filter(arr => arr.id !== i );
                this.setState({
                    seatList : result
                },() => {
                    if(this.state.select === parseInt(this.props.data.number)) this.setcSeat(true);
                    else this.setcSeat(false);
                });
            }
            /*const self = this;
            setTimeout(function(){
                self.setcSeat();
            },10);*/
    }

    deleteSeat = (price,e) => {
        const dataId = parseInt(e.target.dataset.num);
        const result = this.props.data.seatList.filter(arr => arr.id !== dataId);
        console.log(dataId);
        console.log(price);
        this.setState({
            seatList : result,
            select : this.props.data.select - 1,
            price : this.props.data.price - price
        },()=>{
            $('.seat > li').eq(dataId-1).removeClass("active");
            if(this.state.select === parseInt(this.props.data.number)) this.setcSeat(true);
            else this.setcSeat(false);
        });
    }

    render() {
        const { data } = this.props;
        const { poster } = this.props;

        const $row = parseInt(this.props.row);
        const $col = parseInt(this.props.col);
        
        const clearBoth = {clear:"both"}

        let $seat_list = [];
        let $row_list = [];
        let $col_list = [];

        for(let i=1;i<=$row*$col;i++) {
            {poster.detailPrice.map((poster)=>{
                {poster.seatGrade.map((data) => {
                    if(i % $col === 1) {
                        if(!(poster.ground.indexOf(i) === -1)) {
                            if(data.ground){
                                $seat_list.push(<li data-num={i} style={clearBoth} onClick={this.handleSeat.bind(this,i,data.price)}></li>);
                            }
                        }
                        else {
                            if(!data.ground){
                                $seat_list.push(<li data-num={i} style={clearBoth} onClick={this.handleSeat.bind(this,i,data.price)}></li>);
                            }
                        }
                    }
                    else {
                        if(!(poster.ground.indexOf(i) === -1)) {
                            if(data.ground){
                                $seat_list.push(<li data-num={i} onClick={this.handleSeat.bind(this,i,data.price)}></li>);
                            }
                        }
                        else {
                            if(!data.ground){
                                $seat_list.push(<li data-num={i} onClick={this.handleSeat.bind(this,i,data.price)}></li>);
                            }
                        }
                    }
                })}
            })}
        }
        for(let c=1;c<=$col;c++) {
            $col_list.push(<li>{c}</li>);
        }
        for(let r=65;r<65+$row;r++) {
            $row_list.push(<li>{String.fromCharCode(r)}</li>);
        }

        return(
            <section className="reserv-step reserv-step__3">
                <div className="reserv-step__map">
                    <div className="seat-map">
                        <ul className="col-head clearfix">{$col_list}</ul>
                        <ul className="row-head">{$row_list}</ul>
                        <ul className="seat clearfix">{$seat_list}</ul>
                    </div>

                    <div className="map-btns">
                        <a href="javascript:void(0)" className="minus-btn"></a>
                        <a href="javascript:void(0)" className="plus-btn"></a>
                        <a href="javascript:void(0)" className="home-btn"></a>
                    </div>
                </div>
                <div className="reserv-step__right">
                    <div className="seat-info">
                        <h3 className="reserv-box__title">좌석 정보</h3>
                        <ul className="seat-info__list">
                            <li>
                                <em>관람일</em>
                                <p>{data.date}</p>
                            </li>
                            <li>
                                <em>인원수</em>
                                <p>{data.number}</p>
                            </li>
                            <li>
                                <em>연석 사용/미사용</em>
                                <p>연석 미사용</p>
                            </li>
                            {/*<li className="select-list">
                                <em>연석 사용/미사용</em>
                                <select value={data.cSeat} onChange={this.setcSeat}>
                                    <option value="true">연석 사용</option>
                                    <option value="false">연석 미사용</option>
                                </select>
                            </li>*/}
                        </ul>
                    </div>
                    <div className="total-price">
                        <h3 className="reserv-box__title">총 구매 가격</h3>
                        <div className="total-price__box">
                            <ul className="total-price__list">
                                {data.seatList.map(seat => {
                                    return  <li className="clearfix">
                                                <span>{seat.seat}</span>
                                                <span className="seat-price"><small>STAR</small><i className="seat-delete" data-num={seat.id} onClick={this.deleteSeat.bind(this,seat.price)}></i></span>
                                            </li>;
                                })}
                            </ul>
                            <div className="total-box clearfix">
                                <span>총 구매 가격</span>
                                <span className="caculated">{data.price} STAR</span>
                            </div>
                        </div>
                        <p className="seat__alert">*좌석을 선택 후 다음 버튼 클릭시 부터 20분 동안 좌석이 선점되며, 20분 이내에 결제가 이루어지지 않으면 선택 정보가 자동으로 삭제됩니다.</p>
                    </div>
                </div>
            </section>
        );
    }
}

export default Reserv3;