import React, { Component } from 'react';
import $ from 'jquery';

class InquiryForm extends Component {
    componentDidMount() {
        // 1:1문의하기 : 파일 업로드
        $(".inquiry-file__btn").click(function (e) {
            e.preventDefault();
            if ($(this).hasClass('file-add') == true) {
                $("#iFile").click();
            }
        });
        $('.input-file').on('change', function (e) {
            const $add = 'file-add',
                  $remove = 'remove-btn';
            let name = e.target.files[0].name,
                add_remove = $('.inquiry-file__btn'),
                more_btn = $('.inquiry-file__btn.' + $add),
                remove_btn = $('.inquiry-file__btn.' + $remove),
                inq_name = $('.inquiry-file__name');
            $('.inquiry-file__name', $(e.target).parent()).val(name);
            if ($('.inquiry-file__name', '.input-file').val() == "") {
                add_remove.addClass($add).removeClass($remove);
            }
            else {
                add_remove.removeClass($add).addClass($remove);
                inq_name.css('display', 'inline-block');
                $('.inquiry-file__btn.' + $remove).click(function () {
                    $('.input-file').val('');
                    $('.inquiry-file__name', $(e.target).parent()).val('');
                    add_remove.addClass($add).removeClass($remove);
                    inq_name.hide();
                });
            }
        });
    }
    changeToList = () => {
        this.props.onCreate(false);
    }
    render() {
        return(
            <section className="inquiry-form__wrap">
                <h4 className="k-sub__title">1:1 문의하기</h4>
                <form className="inquiry-form">
                    <ul className="inquiry-form__list">
                        <li>
                            <label for="" className="inquiry-form__label">분류</label>
                            <select id="" className="inquiry-form__select">
                                <option default="default" hidden="hidden">카테고리를 선택해 주세요</option>
                                <option>회원정보</option>
                                <option>예매/결제</option>
                                <option>취소/환불</option>
                                <option>발권/배송</option>
                                <option>기타</option>
                            </select>
                        </li>
                        <li>
                            <label for="iTitle" className="inquiry-form__label">제목</label>
                            <input type="text" id="iTitle" placeholder="제목을 입력해주세요.(20자 이내)" className="inquiry-form__input"/>
                        </li>
                        <li>
                            <label for="iName" className="inquiry-form__label">이름</label>
                            <input type="text" id="iName" value="고봉훈" placeholder="이름을 입력해주세요." className="inquiry-form__input"/>
                        </li>
                        <li>
                            <label for="iEmail" className="inquiry-form__label">이메일</label>
                            <input type="email" id="iEmail" value="fotrise@naver.com" placeholder="이메일 주소를 입력해주세요." className="inquiry-form__input"/>
                        </li>
                        <li>
                            <label for="iContent" className="inquiry-form__label">내용</label>
                            <textarea id="iContent" placeholder="문의할 내용을 입력해주세요. (500자 이하로 입력해주세요.)
※ 비회원으로 문의하기 시 입력하신 이메일로만 답변을 받으실 수 있습니다" className="inquiry-form__textarea"></textarea>
                        </li>
                        <li>
                            <label for="iFile" className="inquiry-form__label">첨부파일</label>
                            <input type="file" id="iFile" className="inquiry-form__input input-file"/>
                            <div className="inquiry-file__box">
                                <p className="inquiry-file__subject">첨부파일 추가</p>
                                <a href="javascipt:void(0);" className="inquiry-file__btn file-add txt-hidden">
                                    첨부파일 추가 버튼
                                </a>
                                <input type="text" readOnly="readOnly" className="inquiry-file__name k-truncate"/>
                            </div>
                        </li>
                    </ul>
                    <div className="k-btns">
                        <button className="k-btn cancel-btn" onClick={this.changeToList}>취소</button>
                        <button className="k-btn" type="submit">등록하기</button>
                    </div>
                </form>
            </section>
        );
    }
}

export default InquiryForm;