import React, { Component } from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';


class NoticeList extends Component {
    static defaultProps = {
        data : []
    }

    componentDidMount() {
        $("html,body").scrollTop(0);
        this.props.onCreate("Notice-List");
    }

    render() {
        const { data } = this.props;
        const important = data.slice(0).reverse().map(data => {
            return data.important ? <Link to={"/notice/" + data.id}><ul tabindex="0" className="k-list notice-list k-list__body important-list clearfix">
            <li className="list-num"></li>
            <li className="list-category">{data.category}</li>
            <li className="list-subject">{data.title}</li>
            <li className="list-date">{data.date}</li>
        </ul></Link> : '';
        });
        const list = data.slice(0).reverse().map(data => {
            return  <Link to={"/notice/" + data.id}><ul tabindex="0" className="k-list notice-list k-list__body clearfix">
                        <li className="list-num">{data.id}</li>
                        <li className="list-category">{data.category}</li>
                        <li className="list-subject">{data.title}</li>
                        <li className="list-date">{data.date}</li>
                    </ul></Link>
        });
        return (
            <section className="k-notice__wrap k-section">
                        <select className="notice-category__select">
                            <option>전체</option>
                            <option>안내</option>
                            <option>티켓오픈 안내</option>
                            <option>서비스 소식</option>
                            <option>회사 소식</option>
                        </select>
                        <div className="notice-list__wrap">
                            <ul className="notice-list k-list k-list__hd clearfix">
                                <li className="list-num">번호</li>
                                <li className="list-category">분류</li>
                                <li className="list-subject">제목</li>
                                <li className="list-date">등록일</li>
                            </ul>
                            {important}
                            {list}
                        </div>
                <div className="k-pagination__wrap">
                    <ul className="k-pagination clearfix">
                        <li tabindex="0" className="k-pagination__arrow prev-arrow"></li>
                        <li tabindex="0" className="k-pagination__num active">1</li>
                        <li tabindex="0" className="k-pagination__arrow next-arrow"></li>
                    </ul>
                </div>
            </section>
        );
    }
}

export default NoticeList;